body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  
}
.main_div{
  width: 100%;
  height: 100vh;
  background-color: rgb(201, 173, 173);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.center_div{
  position: absolute;
  width: 25%;
  background-color: #f4f4f4ff;
  height: 77vh;
  box-shadow: 15px 15px 45px -3px rgba(0, 0, 0, 0.8);
  border-radius: 15px;


}
h1{
  background: transparent;
  margin-bottom: 10px;
  margin-top: 1px;
  padding: 6px 0 2px 0;
  color: white;
  text-align: center;
  box-shadow: 5px 5px 15px -5px rgba(0,0,0,0.3);
 font-size: 1.8rem;
background: rgb(175, 47, 70);
}
input{
  border: none;
  border-bottom: 2px solid rgb(175, 47, 70) ;
  height: 30px;
  top: 10px;
  background: transparent;
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  width: 60%;
  outline: none;
}
button{
  border-radius: 50%;
  background:rgb(175, 47, 70); 
  color: white;
  border-color: transparent;
  width: 40px;
  min-height: 20px;
  font-size: 36px;
  outline: none;
  border: none;
  margin-left: 10px;
  box-shadow: 8px 8px 15px 0px rgba(0,0,0,0.3);

}
button:hover{
  background-color: #20bf6b;
}
ol{
  margin-top: 30px;
}
ol li{
  padding-right:20px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  
  align-items: center;
  color:rgb(175, 47, 70); 
  text-transform: capitalize ;
 /*border-bottom: 1px solid rgb(175, 47, 70); */

}
@media (max-width:768px){
  .center_div{
    width: 45%;
  }
}